.bottomLeftArticle {
  width: 372px;
  height: 471px;
  background: white;
  border-radius: 10px;
}

.bottomLeftArticle div {
  display: flex;
  margin-left: 20%;
  padding-top: 50px;
}

.bottomLeftArticle__icon {
  height: 45px;
  width: 45px;
}

.bottomLeftArticle__heading {
  font-size: 11px;
  font-weight: bold;
  text-transform: uppercase;
  padding-left: 15px;
}

.bottomLeftArticle__text {
  color: #7B8591;
  font-size: 16px;
  width: 272px;
  margin: 3em auto;
  text-align: left;
}

.bottomLeftArticle__button {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 25px;
  background-color: white;
  width: 141px;
  height: 49px;
  border: 1px solid #EBEBEB;
  margin: 3em;
}